body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  height: 100%;
  background-image: linear-gradient(to bottom, #1cb9f2, #1cf2eb);
}
.search-box {
    padding-top: 100px;
}
.weather-card {
    color: #fff;
    overflow: hidden;
    font-family: calibri, sans-serif;
    padding: .8rem 1rem ;
    border: 1px solid #ccc;
    border-radius: 5px;
    z-index: 400;
    box-shadow: 0 2px 2px 0 rgba(194, 88, 88, 0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
    margin-bottom: 1rem;
    cursor: pointer;
}

.day-name {
    font-size: 2.5rem;
    font-weight: bold;
    line-height: 3rem;
}

.weather-icon {
    font-size: 7rem;
    text-align: center;
    padding-top: 15px;
    border-bottom: 2px solid #fff;
    padding-bottom: 10px;
}

.weather-day-temp {
    font-size: 6.5rem;
    float: left;
}

.weather-day-temp .units {
    letter-spacing: -0.1em;
}

.weather-details {
    font-size: 2.5rem;
    float: right;
    padding-top: 1.5rem;
}

.weather-details i {
    padding-right: 0.1em;
}

.weather-windspeed .units{
    font-size: 1.5rem;
    letter-spacing: -0.25em;
}

.sunny {
    background-image: radial-gradient(circle farthest-side,#ffdd40,#ffee50);
}

.cloudy {
    background-image: radial-gradient(circle farthest-side,#ccc,#ddd);
}

.rain {
    background-image: radial-gradient(circle farthest-side,#778198,#90aeaa);
}

.lightning {
    background-image: radial-gradient(circle farthest-side, #221d54,#1e3e76);
}

.default {
    background-image: radial-gradient(circle farthest-side, #667db6,#667db6);
}

@media only screen and (min-width: 601px) {
    .weather-card {
        padding: 2rem 3rem;
        margin-bottom: 2rem;
    }

    .day-name {
        font-size: 3rem;
        line-height: 4rem;
    }

    .weather-icon {
        font-size: 8rem;
        padding-top: 15px;
        padding-bottom: 10px;
    }

    .weather-day-temp {
        font-size: 8rem;
    }

    .weather-details {
        font-size: 3.5rem;
        padding-top: 1rem;
    }

    .weather-details i {
        padding-right: 0.1em;
    }

    .weather-windspeed .units{
        font-size: 2rem;
        letter-spacing: -0.25em;
    }

}

@media only screen and (min-width: 601px) {
    .weather-card {
        padding: 1rem 2rem;
    }
    .day-name {
        font-size: 2.5rem;
        line-height: 3.5rem;
    }

    .weather-icon {
        font-size: 5.5rem;
    }
    .weather-day-temp {
        font-size: 4.2rem;
    }

    .weather-details {
        font-size: 1.5rem;
    }

    .weather-windspeed .units {
        font-size: 1rem;
    }

}

@media only screen and (min-width: 993px) {
    .weather-card {
        margin-bottom: 1rem;
        padding: 0.5rem 1rem;
    }

    .day-name {
        font-size: 1.5rem;
        line-height: 2rem;
    }
    .weather-icon {
        font-size: 4rem;
    }
    .weather-day-temp {
        padding-top: .5rem;
        font-size:2.3rem;
    }

    .weather-details {
        font-size: 1.3rem;
        padding-top: 0.5rem;
    }

    .weather-windspeed .units {
        font-size: 0.8rem;
    }
}

@media only screen and (min-width: 1201px) {
    .weather-card {
        padding: 1rem;
    }

    .weather-day-temp {
        padding-top: .2rem;
        font-size: 3rem;
    }

    .weather-details {
        font-size: 1.5rem;
    }
}
.Modal {
    position: fixed;
    overflow: hidden;
    z-index: 500;
    width: 96%;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    padding: 1rem;;
    left: 2%;
    top: 20%;
    transition: all 0.3s ease-out;
}

@media only screen and (min-width: 601px) {
    .Modal {
        width: 70%;
        left: 15%;

    }
}

@media only screen and (min-width: 993px) {
    .Modal {
        width: 60%;
        left: 20%;
    }
}
.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 10;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
}
.weather-details-card {
    color: #fff;
    padding: 0;
    font-family: calibri, sans-serif;
    position: relative;
}


.weather-details-card h3 {
    margin: 0;
    font-weight: bold;
    padding: .5rem 0;
    text-align: center;
}

.close-btn {
    cursor: pointer;
    position: absolute;
    top: 0%;
    right: 0%;
}

.close-btn:hover,
.close-btn:active {
    color: #aaa;
}

.weather-details-day-name {
    font-size: 2.5rem;
    line-height: 2.5rem;
    font-weight: bold;
    text-align: center;
}

.weather-details-day-icon {
    font-size: 5rem;
    text-align: center;
    padding: 1rem 0;
}

.weather-details-day-temp {
    font-size: 2.5rem;
    line-height: 2.5rem;
    font-weight: bold;
}

.weather-hour-list-item {
    display: inline-block;
    font-size: 1.1rem;
    border: 1px solid #fff;
    padding: 0.5rem;
    margin: 0;
    border-left: none;
}

.weather-hour-list-item:first-of-type {
    border-left: 1px solid #fff;
}

@media only screen and (max-width: 500px) {
    .weather-hour-list {
        width: 66%;
        margin-left: 17%;
    }
    .weather-hour-list-item {
        font-size: 1.4rem;
        border: 1px solid #fff;
    }
}

@media only screen and (max-width: 420px) {
    .weather-hour-list {
        width: 98%;
        margin-left: 1%;
    }
}
